.ListHeaders {
  display: flex;
}
.col {
  font-size: 0.8rem;
  margin: 15px auto 5px auto;
  font-weight: bold;
  text-align: center;
}

.subHeading {
  margin: 10px auto 0px auto;
  flex: 1;
  font-size: 0.8rem;
  font-weight: bold;
}

@media (min-width: 500px) {
  .col {
    font-size: 1rem;
  }
  .subHeading {
    font-size: 1rem;
  }
}
